<template>
  <loading :active="isLoading"></loading>
  <div class="container">
    <div class="row">
      <nav>
        <ol class="breadcrumb m-2 py-3 fs-5">
          <li class="breadcrumb-item">
            <router-link to="/" class="text-black">首頁</router-link>
          </li>
          <li class="breadcrumb-item">
            <router-link to="/products" class="text-black">產品</router-link>
          </li>
          <li class="breadcrumb-item active" aria-current="page">
            {{ product.title }}
          </li>
        </ol>
      </nav>
      <div class="col-md-6">
        <!-- swiper -->
        <Swiper
          :style="{
            '--swiper-navigation-color': 'gray',
            '--swiper-pagination-color': 'gray',
          }"
          :slidesPerView="1"
          :spaceBetween="30"
          :loop="true"
          :navigation="true"
          :thumbs="{ swiper: thumbsSwiper }"
          :modules="modules"
          class="mySwiper"
        >
          <SwiperSlide
            ><img
              :src="product.imageUrl"
              :alt="product.title"
            />
          </SwiperSlide>
          <SwiperSlide v-for="(image, key) in product.images" :key="image">
            <img
              :src="image"
              :alt="`${product.title} ${key}`"
              class="img-fluid"
            />
          </SwiperSlide>
        </Swiper>
        <!-- swiper -->

        
        <!-- swiper -->
        
      </div>
      <div class="col-md-6">
        <div>
          <div class="d-flex justify-content-between align-items-end">
            <span class="fw-bold h1 mb-2">{{ product.title }}
            </span>
            <span class="badge pill bg-brown fs-4 mb-2 text-end">{{
              product.category
            }}</span>
          </div>
          <hr />
          <p>
            <h3 class="my-2 fw-bold">商品描述</h3>
            <span class="fs-5 fw-bold">{{ product.description }}</span>
            <br>
            <h3 class="my-2 fw-bold">商品內容</h3>
            <p class="text-muted fw-bold">{{ product.content }}</p>
          </p>
          <div class="d-flex justify-content-end mb-3">
          <template v-if="product.price !== product.origin_price">
            <div class="d-flex align-items-center fw-bold">
              <h2 class="h1 mb-0 text-danger me-2 ">NT${{ product.price }}</h2>
              <h3 class="h5 mb-0 text-decoration-line-through text-muted">
                NT${{ product.origin_price }}
              </h3>
            </div>
          </template>
          <h2 class="h1 mb-0" v-else>NT${{ product.price }}</h2>
        </div>
          <div class="row align-items-center">
            <div class="col-12">
              <div class="input-group my-3 bg-light rounded">
                <button
                  class="btn btn-outline-dark border-0 py-2"
                  type="button"
                  @click="minus"
                >
                  <i class="bi bi-dash"></i>
                </button>
                <input
                  type="number"
                  class="form-control text-center"
                  min="1"
                  :value="count"
                />
                <button
                  class="btn btn-outline-dark border-0 py-2"
                  type="button"
                  @click="plus"
                >
                  <i class="bi bi-plus"></i>
                </button>
              </div>
            </div>
            <div class="col-12">
              <button
                class="text-nowrap btn btn-danger w-100 py-2"
                @click="addCart(product.id)"
              >
                加入購物車
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
<!--  -->
<h1 class="my-5 fw-bold">常見問題</h1>
<div class="nav nav-pills"  >
    <button class="btn btn-brown rounded-0 w-50 active fs-4" data-bs-toggle="pill" data-bs-target="#pills1" type="button">保存方式</button>
    <button class="btn btn-brown rounded-0 w-50 fs-4" data-bs-toggle="pill" data-bs-target="#pills2" type="button">商品到貨時間</button>
</div>
<div class="tab-content bg-tan p-2">
  <div class="tab-pane active" id="pills1">
    <p class="fw-bold">
      蛋糕類 : 冷藏保存3天，冷凍保存5天<br/>
      酥皮類 : 冷藏保存3天，冷凍保存6天<br/>
      巧克力類 : 冷藏保存2天，冷凍保存30天<br/>
      布丁類 : 冷藏保存3天
    </p>
  </div>
  <div class="tab-pane" id="pills2" >
    <p class="fw-bold">訂單完成並成功付款後，商品將於5日內(不含假日)送到您的指定地點</p>
  </div>
</div>
<!--  -->
    <div class="row my-5">
      <Sametype
      :product="product"
      @update="getProduct" />
    </div>
  </div>
</template>
<script>
import Toast from "@/utils/Toast";
import Sametype from "@/components/Sametype.vue";
import emitter from "@/methods/emitter";
import { Swiper, SwiperSlide } from "swiper/vue";
import "swiper/css";
import "swiper/css/pagination";
import "swiper/css/navigation";
import SwiperCore, { Navigation, Autoplay, Pagination } from "swiper/core";
SwiperCore.use([Navigation, Autoplay, Pagination]);
export default {
  components: {
    Swiper,
    SwiperSlide,
    Sametype,
  },
  data() {
    return {
      product: {},
      products: [],
      isLoading: false,
      id: "",
      count: 1,
      message: [],
    };
  },
  inject: ["emitter", "$httpMessageState"],
  methods: {
    getProduct() {
      const { id } = this.$route.params;
      const api = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/product/${id}`;
        // console.log(api);
      this.isLoading = true;
      this.$http
        .get(api)
        .then((res) => {
          // console.log(res);
          this.isLoading = false;
          this.product = res.data.product;
        })
        .catch(() => {
          this.isLoading = false;
        });
    },
    plus() {
      this.count += 1;
    },
    minus() {
      if (this.count > 1) {
        this.count -= 1;
      }
    },
    addCart(id) {
      // console.log(id);
      const url = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/cart`;
      this.isLoading = true;
      const cart = {
        product_id: id,
        qty: this.count,
      };
      this.$http.post(url, { data: cart }).then((res) => {
        this.isLoading = false;
        console.log(res);
        this.message = res.data.message;
        // this.$httpMessageState(res);
        Toast.fire({
          icon: 'success',
          title: this.message,
        });
        emitter.emit("get-cart");
      });
    },
    getProducts() {
      
      const url = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/products/all`;

      this.$http.get(url).then((res) => {
        this.products = res.data.products;
      });
    },
    gerId(id){
      console.log(id);
      this.$router.push(`/product/${id}`);
      this.getProduct(id);
    }
  },
  created() {
    this.getProduct();
  },
};
</script>

<style>
.swiper {
  width: 100%;
  height: 100%;
  
}

.swiper-slide {
  text-align: center;
  font-size: 18px;
  background: #fff;

  /* Center slide text vertically */
  display: flex;
  justify-content: center;
  align-items: center;
}

.swiper-slide img {
  display: block;
  width: 100%;
  /* max-height: 400px; */
  object-fit: cover;
}

</style>
